import React from 'react';

import TextField from '@mui/material/TextField';

import {
    Button,
    Radio,
    RadioGroup,
    FormControlLabel,
    FormControl,
    InputLabel,
    Select,
    FormLabel,
    FormHelperText,
    FormGroup,
    Checkbox,
    Container,
    Box,
    Stack,
    MenuItem,
} from '@mui/material';

// ----------------------------------------------------------------------

import { useForm } from "react-hook-form";

export default function Edit(props) {
    const { inserdata, recordForEdit, setOpenPopup } = props

    const handleCancel = () => {
        return setOpenPopup(false)
    }
    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    const onSubmit = (data) => inserdata(data);



    return (
        <>


            <Container maxWidth="lg" sx={{ margin: 0, }}>
                <Box
                    sx={{ padding: '10px', borderRadius: '10px' }}
                >

                    <form onSubmit={handleSubmit(onSubmit)}>
                        <TextField
                            id="outlined-basic"
                            name="id"
                            label="Customer ID"
                            variant="outlined"
                            fullWidth
                            {...register("id", { required: "ID is required." })}
                            error={Boolean(errors.id)}
                            defaultValue={recordForEdit.id}
                            helperText={errors.id?.message}
                        />
                        <br/>
                        <br/>
                        <Stack direction="row" spacing={2}>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="fullName"
                                    label="Full Name"
                                    variant="outlined"
                                    fullWidth
                                    {...register("Name", { required: "FullName is required." })}
                                    error={Boolean(errors.Name)}
                                    defaultValue={recordForEdit.name}
                                    helperText={errors.Name?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Address"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.homeaddress}
                                    name="FullAddress"
                                    {...register("FullAddress", { required: "Address is required." })}
                                    error={Boolean(errors.FullAddress)}
                                    helperText={errors.FullAddress?.message}
                                />
                            </Box>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="mobile"
                                    label="Mobile"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.mobile}
                                    {...register("Mobile", { required: "Mobile is required." })}
                                    error={Boolean(errors.Mobile)}
                                    helperText={errors.Mobile?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Telephone Office"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.mobile}
                                    name="TelOffice"
                                    {...register("TelOffice", { required: "Telephone Office is required." })}
                                    error={Boolean(errors.TelOffice)}
                                    helperText={errors.TelOffice?.message}
                                />
                            </Box>
                        </Stack>
                        <br />
                        <Stack direction="row" spacing={2}>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="employmenttype"
                                    label="Employment Type"
                                    variant="outlined"
                                    defaultValue={recordForEdit.employmenttype}
                                    fullWidth
                                    {...register("employmenttype", { required: "Employment Type is required." })}
                                    error={Boolean(errors.employmenttype)}
                                    helperText={errors.employmenttype?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Employer Name"
                                    variant="outlined"
                                    fullWidth
                                    name="employername"
                                    defaultValue={recordForEdit.employer}
                                    {...register("EmployerName", { required: "Employer Name is required." })}
                                    error={Boolean(errors.EmployerName)}
                                    helperText={errors.EmployerName?.message}
                                />
                            </Box>
                          

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Net Salary Current Job"
                                    variant="outlined"
                                    fullWidth
                                    name="Netmonthsalary"
                                    defaultValue={recordForEdit.netmonthsalary}
                                    {...register("Netmonthsalary", { required: "Net salary is required." })}
                                    error={Boolean(errors.Netmonthsalary)}
                                    helperText={errors.Netmonthsalary?.message}
                                />

                            </Box>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Position"
                                    variant="outlined"
                                    fullWidth
                                    name="Position"
                                    defaultValue={recordForEdit.position}
                                    {...register("Position", { required: "Position is required." })}
                                    error={Boolean(errors.Position)}
                                    helperText={errors.Position?.message}
                                />

                            </Box>
                        </Stack>
                        <br />
                        <Stack direction="row" spacing={2}>

                            <Box flex={4}>
                                <FormControl sx={{ width: '110px' }}>
                                    <InputLabel id="possitionId-label">Citizenship</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="countryid"
                                        label="Citizenship"
                                        sx={{ width: '228px' }}
                                        defaultValue={recordForEdit.citizenship}
                                        {...register("citizenship", { required: "Citizenship is required." })}
                                        error={Boolean(errors.citizenship)}
                                        helperText={errors.citizenship?.message}
                                    >
                                        <MenuItem value={'Somali'}>Somali</MenuItem>
                                        <MenuItem value={'Ethiopian'}>Ethiopian</MenuItem>
                                    </Select>

                                </FormControl>
                            </Box>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="landmark"
                                    label="Other Banks"
                                    variant="outlined"
                                    defaultValue={recordForEdit.otherbanks}
                                    fullWidth
                                    {...register("otherbanks", { required: "Other Banks is required." })}
                                    error={Boolean(errors.otherbanks)}
                                    helperText={errors.otherbanks?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Date Joining"
                                    variant="outlined"
                                    type="date"
                                    fullWidth
                              
                                    name="Datejoining"
                                    defaultValue={recordForEdit.datejoining}
                                    {...register("Datejoining", { required: "Date of joining current job is required." })}
                                    error={Boolean(errors.Datejoining)}
                                    helperText={errors.Datejoining?.message}
                                />
                            </Box>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Previous Employer"
                                    variant="outlined"
                                    fullWidth
                                    name="Previousemployer" 
                                    defaultValue={recordForEdit.preemployer}
                                    {...register("Previousemployer", { required: "Previous Employer is required." })}
                                    error={Boolean(errors.Previousemployer)}
                                    helperText={errors.Previousemployer?.message}
                                />
                            </Box>
                          

                        </Stack>
                        <br />

                        <br />
                        <Stack direction="row" spacing={2}>
                          

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Other Income"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.otherincome}
                                    name="otherincome"
                                    {...register("otherincome", { required: "Other income is required." })}
                                    error={Boolean(errors.otherincome)}
                                    helperText={errors.otherincome?.message}
                                />
                            </Box>


                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="LandMark"
                                    label="Land Mark"
                                    variant="outlined"
                                    defaultValue={recordForEdit.otherincome}
                                    {...register("LandMark", { required: "Land Markis required." })}
                                    error={Boolean(errors.LandMark)}
                                    helperText={errors.LandMark?.message}
                                />
                            </Box>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="EAB date opened"
                                    variant="outlined"
                                    type="date"
                                    fullWidth
                                    name="EABdateopened"
                                    defaultValue={recordForEdit.eabopenedaccount}
                                    {...register("EABdateopened", { required: "Date of brith is required." })}
                                    error={Boolean(errors.EABdateopened)}
                                    helperText={errors.EABdateopened?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Liabilities Other Banks"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.liabilities}
                                    name="liabilitiesotherbanks"
                                    {...register("liabilitiesotherbanks", { required: "Liabilities Other Banks is required." })}
                                    error={Boolean(errors.liabilitiesotherbanks)}
                                    helperText={errors.liabilitiesotherbanks?.message}
                                />
                            </Box>

                        </Stack>
                        <br />
                      
                        <br />
                        <Stack direction="row" spacing={2}>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Account Number"
                                    variant="outlined"
                                    fullWidth
                                    defaultValue={recordForEdit.account}
                                    name="account"
                                    {...register("account", { required: "Account number is required." })}
                                    error={Boolean(errors.account)}
                                    helperText={errors.account?.message}
                                />
                            </Box>
                            <Box flex={4}>
                                <FormControl sx={{ width: '110px' }}>
                                    <InputLabel id="possitionId-label">Salary Credited EAB</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="countryid"
                                        label="Salary credit EAB"
                                        sx={{ width: '250px' }}
                                        defaultValue={recordForEdit.salarycrediteab}
                                        {...register("salarycrediteab", { required: "Salary credit eab is required." })}
                                        error={Boolean(errors.salarycrediteab)}
                                        helperText={errors.salarycrediteab?.message}
                                    >
                                        <MenuItem value={'Yes'}>Yes</MenuItem>
                                        <MenuItem value={'No'}>No</MenuItem>
                                    </Select>

                                </FormControl>
                            </Box>
                            <Box flex={4}>
                                {/* Radio button */}
                                <FormControl error={Boolean(errors.gender)} >
                                    <FormLabel component="legend"> Choose Your Gender </FormLabel>
                                    <RadioGroup defaultValue={recordForEdit.gender} row aria-label="gender" name="gender">
                                        <FormControlLabel
                                            value="female"
                                          
                                            control={
                                                <Radio {...register("gender", { required: "Choose your gender" })} />
                                            }
                                            label="Female"
                                        />
                                        <FormControlLabel
                                            value="male"
                                            control={
                                                <Radio {...register("gender", { required: "Choose your gender" })} />
                                            }
                                            label="Male"
                                        />


                                    </RadioGroup>
                                    <FormHelperText style={{ color: '#d32f2f' }}>{errors.gender?.message}</FormHelperText>
                                </FormControl>
                            </Box>
                        </Stack>

                        <Stack direction="row" spacing={2}>

                            <Button variant="contained" color="primary" type="submit" className="btns">
                                Update
                            </Button>
                            <Button variant="contained" color="error" onClick={handleCancel} className="btns">
                                Cancel
                            </Button>
                        </Stack>


                    </form>
                </Box>
            </Container>
        </>
    );
}
